.footer-contacts {
  max-width: 311px;

  @include vp-1023 {
    max-width: 100%;
  }

  @include vp-768 {
    font-size: 14px;
  }
}

.footer-contacts__title {
  padding-left: 3px;
  margin-top: 10px;
  margin-bottom: 20px;

  font-family: "Roboto Condensed", "arial", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;

  text-transform: uppercase;

  @include vp-1023 {
    margin-top: 15px;
  }
}

.footer-contacts__phone-list {
  padding: 0;
  margin: 0;
  padding-left: 3px;

  list-style: none;
  font-style: normal;

  @include vp-768 {
    padding-left: 25px;
  }
}

.footer-contacts__phone-item {
  margin-bottom: 10px;
}

.footer-contacts__phone-link {
  color: $color-white-text;
  opacity: 0.7;

  &:hover {
    text-decoration: underline;
    opacity: 0.9;
  }

  &:active {
    opacity: 0.4;
  }
}

.footer-contacts__address {
  padding-left: 3px;
  margin: 0;
  margin-bottom: 10px;

  font-style: normal;

  @include vp-768 {
    padding-left: 25px;
    margin-bottom: 15px;

    line-height: 19px;
  }
}
